<template>
    <div class="container">
      <!-- 주소 입력란 -->
      <div class="input-group mb-3" style="margin: 10px">
        <input
          type="text"
          class="form-control"
          placeholder="주소를 입력하세요"
          v-model="address"
        />
        <div class="input-group-append">
          <button
            class="btn btn-outline-secondary"
            type="button"
            @click="searchAddress"
          >
            검색
          </button>
        </div>
      </div>
  
      <!-- 카테고리 목록 -->
      <div class="card" style="margin: 10px">
        <div class="row">
          <div
            class="col-4 col-md-3 category-card"
            v-for="category in categories"
            :key="category.id"
            @click="goToCategory(category.id)"
          >
            <img
              class="card-img-top"
              :src="category.image"
              :alt="category.name"
            />
            <div class="card-body">
              <h6 class="card-title">{{ category.name }}</h6>
            </div>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    name: "C_MainCategory",
    data() {
      return {
        address: "", // 주소를 저장할 데이터
        categories: [
        //   {
        //     id: "Kfood",
        //     name: "한식",
        //     image: require("@/assets/img/food_img/Kfood.png"),
        //   },
        //   {
        //     id: "Wfood",
        //     name: "양식",
        //     image: require("@/assets/img/food_img/Wfood.png"),
        //   },
        //   {
        //     id: "Jfood",
        //     name: "일식",
        //     image: require("@/assets/img/food_img/Jfood.png"),
        //   },
        //   {
        //     id: "Cfood",
        //     name: "중식",
        //     image: require("@/assets/img/food_img/Cfood.png"),
        //   },
        //   {
        //     id: "KSfood",
        //     name: "분식",
        //     image: require("@/assets/img/food_img/KSfood.png"),
        //   },
          // 다른 카테고리들...
        ],
      };
    },
    methods: {
      searchAddress() {
        new window.daum.Postcode({
        oncomplete: (data) => {
          this.address = data.roadAddress; // 선택한 주소를 address에 저장
        }
        }).open();
      },
      goToCategory(categoryId) {
        // 카테고리 클릭 시 해당 카테고리 페이지로 이동
        this.$router.push(`/${categoryId}`);
      },
    },
  };
  </script>
  
  <style>
  @media (min-width: 768px) {
    /* 화면 크기가 768px 이상일 때만 적용 */
    .category-card {
      margin-top: 20px;
      margin-right: 20px; /* 카테고리 카드 간의 간격 설정 */
      margin-bottom: 20px;
    }
  }
  </style>